<template>
  <fixed-layout>
    <template slot="header">
      <!--<topbar>-->
        <!--<template slot="before">-->
          <!--<b class="iconfont icon-back" @click="$router.go(-1)"></b>-->
        <!--</template>-->
        <!--俱乐部认证-->
        <!--<template slot="after">-->
          <!--&lt;!&ndash;<button class="btn" @click="handNext">发送</button>&ndash;&gt;-->
        <!--</template>-->
      <!--</topbar>-->
      <mt-header title="俱乐部认证">
        <router-link to="/" slot="left">
          <mt-button icon="back" @click="$router.go(-1)"></mt-button>
        </router-link>
        <!--<mt-button @click="handNext" slot="right">下一步</mt-button>-->
      </mt-header>
    </template>
    <mt-cell
      title="选择俱乐部类型"
      :value="clubType.label"
      is-link
      @click.native="sheetClubType.visible=true">
    </mt-cell>
    <mt-actionsheet
      cancelText=""
      :actions="sheetClubType.actions"
      v-model="sheetClubType.visible">
    </mt-actionsheet>
    <mt-field label="俱乐部名称" placeholder="请输入俱乐部名称" class="right" v-model="name"></mt-field>
    <mt-field label="俱乐部所在城市" placeholder="请输入俱乐部所在城市" class="right" v-model="city"></mt-field>
    <mt-field type="tel" label="俱乐部联系方式" placeholder="请填写正确的手机号码用于收取短信" v-model="phone"></mt-field>
    <mt-field type="mail" label="邮箱" placeholder="请填写邮箱" class="right" v-model="email"></mt-field>
    <mt-field label="微信号" placeholder="请填写微信号" class="right" v-model="wxId"></mt-field>
    <mt-field type="number" label="银行账号" placeholder="请填写银行账号" class="right" v-model="bankNo"></mt-field>
    <mt-cell title="俱乐部头像"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <template v-if="headimg.url||headimg.base64">
            <section class="uploadImage">
              <img :src="headimg.url||headimg.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(headimg)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'headimg')"></file-image>
          </template>
        </li>
      </ul>
    </section>
    <mt-cell title="俱乐部主页图" value="请上传横图"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <template v-if="mainimg.url||mainimg.base64">
            <section class="uploadImage">
              <img :src="mainimg.url||mainimg.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(mainimg)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'mainimg')"></file-image>
          </template>
        </li>
      </ul>
    </section>
    <mt-cell title="俱乐部简介"></mt-cell>
    <mt-field type="textarea" placeholder="请填写俱乐部简介" v-model="desc"></mt-field>
    <mt-cell title="上传认证材料" value="请上传您的身份证正反面和营业执照"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <template v-if="mainimg.url||mainimg.base64">
            <section class="uploadImage">
              <img :src="mainimg.url||mainimg.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(mainimg)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'idcardF')"></file-image>
          </template>
          <h4>身份证正面</h4>
        </li>
        <li>
          <template v-if="idcardB.url||idcardB.base64">
            <section class="uploadImage">
              <img :src="idcardB.url||idcardB.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(idcardB)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'idcardB')"></file-image>
          </template>
          <h4>身份证反面</h4>
        </li>
        <li>
          <template v-if="licenceimg.url||licenceimg.base64">
            <section class="uploadImage">
              <img :src="licenceimg.url||licenceimg.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(licenceimg)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'licenceimg')"></file-image>
          </template>
          <h4>营业执照</h4>
        </li>
      </ul>
    </section>
    <section class="ft">
      <p class="agreement"><input type="checkbox"/><a href="">同意《号角平台商户服务协议》</a></p>
      <mt-button type="primary" @click="handSubmit">确认</mt-button>
    </section>
  </fixed-layout>
</template>

<script>
  import {mapState} from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import FileImage from '@/components/file-image.vue'

  export default {
    name: 'club',
    components: {
      FixedLayout,
      Topbar,
      FileImage
    },
    data() {
      return {
        clubType: {
          value: -1,
          label: '请选择'
        },
        sheetClubType: {
          visible: false,
          actions: []
        },
        name: '',
        city: '',
        phone: null,
        email: '',
        wxId: '',
        bankNo: null,
        desc: '',
        headimg: {
          file: null,
          base64: '',
          url: ''
        },
        mainimg: {
          file: null,
          base64: '',
          url: ''
        },
        idcardF: {
          file: null,
          base64: '',
          url: ''
        },
        idcardB: {
          file: null,
          base64: '',
          url: ''
        },
        licenceimg: {
          file: null,
          base64: '',
          url: ''
        },
      }
    },
    computed: {
      ...mapState({
        ClubType: state => state.config.ClubType,
      })
    },
    watch: {
      ClubType: {
        immediate: true,
        handler(value) {
          value.forEach(e => {
            this.sheetClubType.actions.push({
              name: e.label,
              method: () => {
                this.clubType = e
              }
            })
          })
        }
      },
    },
    created() {
      if (this.$route.query.clubId) {
        this.$http.get(this.$port.AUTH_CLUB, {
          params: {
            clubId: this.$route.query.clubId
          }
        }).then(res => {
          res.code = 200
          res.data = {
            city: 123,
            headimg: 'https://www.baidu.com/img/bd_logo1.png?where=super',
            mainimg: 'https://www.baidu.com/img/bd_logo1.png?where=super',
            idcardF: 'https://www.baidu.com/img/bd_logo1.png?where=super',
            idcardB: 'https://www.baidu.com/img/bd_logo1.png?where=super',
            licenceimg: 'https://www.baidu.com/img/bd_logo1.png?where=super',
          }
          if (res.code === 200) {
            this.clubType.value = res.data.clubType
            this.name = res.data.name
            this.city = res.data.city
            this.phone = res.data.phone
            this.email = res.data.email
            this.wxId = res.data.wxId
            this.bankNo = res.data.bankNo
            this.desc = res.data.desc
            this.headimg.url = res.data.headimg
            this.mainimg.url = res.data.mainimg
            this.idcardF.url = res.data.idcardF
            this.idcardB.url = res.data.idcardB
            this.licenceimg.url = res.data.licenceimg
          }
        })
      }
    },
    methods: {
      handDeleteImage(obj) {
        obj.file = null
        obj.base64 = ''
        obj.url = ''
      },
      uploadImg(file) {
        return new Promise((resolve, reject) => {
          let fd = new FormData()
          fd.append('imgType', 1)
          fd.append('upload', file)
          this.$http.post(this.$port.UPLOAD_IMG, fd).then(res => {
            resolve(res)
          })
        })
      },
      handLoadImg(e, file, prop) {
        console.log('handLoadImg', e, file, prop)
        this[prop].file = file
        this[prop].base64 = e.currentTarget.result
        this.uploadImg(file).then(res => {
          if (res.code === 200) {
            this[prop].url = res.data
          } else {
            alert(res.msg)
          }
        })
      },
      handSubmit() {
        let params = {
          clubId: 0,
          clubType: this.clubType.value,
          name: this.name,
          city: this.city,
          phone: this.phone,
          email: this.email,
          wxId: this.wxId,
          bankNo: this.bankNo,
          headimg: this.headimg.url,
          mainimg: this.mainimg.url,
          idcardF: this.idcardF.url,
          idcardB: this.idcardB.url,
          licenceimg: this.licenceimg.url,
          desc: this.desc
        }
        this.$http.post(this.$port.AUTH_CLUB, this.$qs.stringify(params)).then(res => {
          if (res.code === 200) {

          }
          alert(res.msg)
        })
      }
    }
  }
</script>
<style lang="scss">
</style>
<style lang="scss" scoped>
  .m-uploads {
    background-color: #fff;
    padding: 0 10px;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        position: relative;
        flex: 1;
        b {
          background-color: rgba(0, 0, 0, 0.5);
          text-align: center;
          font-weight: 400;
          font-size: 8px;
          color: #fff;
          width: 20px;
          height: 20px;
          position: absolute;
          right: 0;
          top: 0;
        }
        h4 {
          font-size: 12px;
          text-align: center;
          font-weight: 400;
          color: #333;
          padding-top: 5px;
        }
      }
    }
    .ui-upload {
      width: 110px;
      height: 110px;
    }
  }

  .uploadImage {
    width: 110px;
    height: 110px;
    border: solid #e5e5e5 1px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      display: block;
    }
  }

  .ft {
    padding: 15px 10px;
    border-top: solid #e5e5e5 1px;
    .agreement {
      font-size: 12px;
      margin-bottom: 10px;
      input[type='checkbox'] {
        margin-right: 5px;
        vertical-align: middle;
      }
    }
    .mint-button {
      width: 100%;
    }
  }
</style>



